const Test = () => {

  const [foundContent, setFoundContent] = useState('');

  // Sample HTML content
  const sampleContent = `
    <div> hello world </div>
    <div class="test-class">Testing search functionality</div>
    <span class="highlight">Multiple world instances in world text</span>
    <p>This is a paragraph with some World text to search through.</p>
    <a href="https://example.com">Test Link</a>
  `;

const search = () =>{
  setFoundContent(highlightText(sampleContent, 'Test'));
  console.log("Button is clicked")
}
  return (
   <>
    <button onClick={search} >Search</button>

<Link to ='test2' className="text-blue-500">Go to Test2</Link>

    <div dangerouslySetInnerHTML={{ __html: foundContent }} />
   </>
  );
};

export default Test;